import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { Http } from '@angular/http';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';

import { ShareButtons } from '@ngx-share/core';
import { faTwitter, faFacebook } from '@fortawesome/free-brands-svg-icons';
import { library } from '@fortawesome/fontawesome-svg-core';

@Component({
  selector: 'app-donation-page',
  templateUrl: './donation-page.component.html',
  styleUrls: ['./donation-page.component.css'],

})
export class DonationPageComponent{
  public donateAmount:number=50;
  public progressValue:number =833;
  public amt:number;
  public progressLabel:any;
  public accessSocialShare=true;


  constructor(public toastr:ToastsManager,public vcr:ViewContainerRef, public share: ShareButtons){
    library.add(faTwitter,faFacebook);
    this.toastr.setRootViewContainerRef(vcr);
    this.amt= 1000-this.progressValue;
    this.progressLabel=(100-(this.amt/20))+'%';
  }

  public openDialog():void {
    
    this.accessSocialShare=(this.accessSocialShare==true)?false:true;
    
  }
  
  public saved():any{

    this.toastr.success('Saved!');
    
  }

  public update():any{

    if((this.progressValue +this.donateAmount)<1000){
      this.progressValue += this.donateAmount;
    }
     
    this.amt= 1000-this.progressValue;
    this.progressLabel=(100-(this.amt/20))+'%';

  }

}



